
import XButton from 'vux/src/components/x-button/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Msg from 'vux/src/components/msg/index.vue'

import { mapState } from 'vuex';

export default {
components: {
XButton,
CellFormPreview,
Group,
CellBox,
Cell,
Msg
},
data: function() {
return {
isShowLoading: false,
isShowSuccess: false,
list: [
{
label: '单价',
value: '0.5元/张'
},
{
label: '页数',
value: '4'
},
{
label: '份数',
value: '2'
}
],
icon: '',
buttons: [
{
type: 'primary',
text: '我的订单',
onClick: this.changeIcon.bind(this)
},
{
type: 'default',
text: '继续预约',
link: '/index'
}
]
};
},
computed: {
...mapState({
storeData: (state) => state.printData.storeData,
fileData: (state) => state.printData.fileData
})
},
methods: {
onBack() {
this.$router.push({
path: '/setconfig'
});
},
onPayment() {
this.$vux.toast.show({
text: '调用微信支付',
type: 'text'
});
this.isShowLoading = true;
setTimeout(() => {
this.isShowSuccess = true;
this.isShowLoading = false;
}, 3000);
},
changeIcon() {
if (!this.icon || this.icon === 'success') {
this.icon = 'warn';
return;
}
if (this.icon === 'warn') {
this.icon = 'info';
return;
}
if (this.icon === 'info') {
this.icon = 'waiting';
return;
}
if (this.icon === 'waiting') {
this.icon = 'success';
}
}
},
created: function() {}
};



